import React from "react";
import { fade, makeStyles } from "@material-ui/core";
import SearchIcon from "@material-ui/icons/Search";
import SearchBar from "../SearchBar/searchBar";
import { Navbar, Nav, NavDropdown } from "react-bootstrap";
import { Link } from "react-router-dom";
import "./navbar.css";

const useStyles = makeStyles((theme) => ({
  root: {
    flexGrow: 5,
  },
  home: {
    flexGrow: 1,
    marginLeft: theme.spacing(3),
    paddingBottom: 4,
  },
  title: {
    display: "none",
    [theme.breakpoints.up("sm")]: {
      display: "block",
    },
  },
  search: {
    position: "relative",
    borderRadius: theme.shape.borderRadius,
    backgroundColor: fade(theme.palette.common.white, 0.15),
    "&:hover": {
      backgroundColor: fade(theme.palette.common.white, 0.25),
    },
    marginLeft: 0,
    width: "100%",
    [theme.breakpoints.up("sm")]: {
      marginLeft: theme.spacing(1),
      width: "auto",
    },
  },
  searchIcon: {
    padding: theme.spacing(0, 2),
    height: "100%",
    position: "absolute",
    pointerEvents: "none",
    display: "flex",
    alignItems: "center",
    justifyContent: "center",
  },
}));

export default function Navbaar() {
  const classes = useStyles();

  return (
    <Navbar className="navbar__body" collapseOnSelect expand="lg" sticky="top">
      <Navbar.Brand className="brandd">MOVIESPOT</Navbar.Brand>
      <Navbar.Toggle
        aria-controls="responsive-navbar-nav"
        className="nav__toggle"
      />
      <Navbar.Collapse id="responsive-navbar-nav">
        <Nav className="mr-auto">
          <Nav.Link as={Link} to="/movies" className="nav__link">
            Movies
          </Nav.Link>
          <NavDropdown
            title="TV Shows"
            id="collasible-nav-dropdown"
            className="nav__dropdown"
          >
            <NavDropdown.Item as={Link} to="/tv/popular">
              Popular
            </NavDropdown.Item>

            <NavDropdown.Item
              as={Link}
              to="/tv/airing-today"
              style={{ color: "black" }}
            >
              Airing Today
            </NavDropdown.Item>
            <NavDropdown.Item
              as={Link}
              to="/tv/on-tv"
              style={{ color: "black" }}
            >
              On Tv
            </NavDropdown.Item>
            <NavDropdown.Item
              as={Link}
              to="/tv/top-rated"
              style={{ color: "black" }}
            >
              Top Rated
            </NavDropdown.Item>
          </NavDropdown>
        </Nav>
        <div className={classes.search}>
          <div className={classes.searchIcon}>
            <SearchIcon />
          </div>
          <SearchBar />
        </div>
      </Navbar.Collapse>
    </Navbar>
  );
}
